
.UserGames {
    background: #141416;
    border-radius: 5px;
    border-collapse: collapse;
    width: 100%;
    overflow-x: auto;
    white-space: nowrap;
    border-radius: 5px;
}

.UserGames thead tr:first-child th:first-child {
    border-top-left-radius: 5px;
}
.UserGames thead tr:last-child th:last-child {
    border-top-right-radius: 5px;
}
.UserGames tr:last-child td:first-child {
    border-bottom-left-radius: 5px;
}
.UserGames tr:last-child td:last-child {
    border-bottom-right-radius: 5px;
}


.UserGames tbody td {
    padding: 7px 15px;
}

.UserGames thead th {
    padding: 10px 0;
}

.UserGames .game img {
    width: 30px;
    height: 30px;
    object-fit: cover;
}


.openFairnessB {
    padding: 8px 20px;
    background-color: #59bd7ee2;
    color: #F5F5F5;
    border: none;
    border-radius: 99px;
}
.Profile .info {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Profile .gameStats {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 90%;
}

.Profile .gameStat {
    color: rgba(211, 211, 211, 0.849);
}
.UserGames .win {
    color: rgba(37, 185, 0, 0.85);
}

.UserGames .lose {
    color: rgba(185, 37, 0, 0.85);
}

.UserGames .tdContent {
    display: flex;
    justify-content: center;
    align-items: center;
}

.UserGames .bet svg {
    width: 20px;
    height: 20px;
    margin-left: 5px;
    object-fit: cover;
}
.UserGames .coin img {
    width: 40px;
    height: 40px;
    object-fit: cover;
}

@media (max-width: 880px) {
    .UserGames {
        display: none;
    }
}