.Chat {
    position: fixed;
    top: 120px;
    right: 20px;
    height: calc(75vh - 80px);
    background: #141416;
    width: 265px;
    scrollbar-width: 1px;
    border-radius: 5px;
}
    .Chat .chat {
        display: flex;
        flex-direction: column;
        height: 100%;
        padding: 0 5px;
        padding-left: 10px;
        padding-top: 10px;
        justify-content: space-between;
    }
    .Chat .chatHeader {
        padding: 10px;
        padding-top: 0;
    }
    .Chat .sendMsgB {
        width: 30px;
        height: 30px;
        margin-left: 10px;
        color: white;
        font-weight: 600;
        background: #232531;
        border: none;
        border-radius: 5px;
    }
    .Chat .sendMsgB svg {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    .Chat .chatHeader h2 {
        margin: 0
    }
    .Chat .chat .messages {
        overflow-y: scroll;
    }
    .Chat .chat .inputMessage {
        padding: 20px 0;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .Chat .chat .inputMessage .timer {
        margin-left: 10px;
    }
    .chat .inputMessage textarea {
        background: #232531;
        border: none;
        color: white;
        border-radius: 99px;
        padding: 6px 16px;
        resize: none;
        width: 180px;
        min-height: 30px;
        max-height: 200px;

    }
    .chat .inputMessage textarea:focus {
        outline: none;
    }
    .chat .message {
        display: flex;
        align-items: center;
        background-color: #232531;
        border-radius: 5px;
        margin-bottom: 13px;
        padding: 0 5px;
    }
    .chat .message .content {
        word-break: break-all;
        padding: 5px 5px;
    }
    .chat .message .content .text {
        margin: 0;
    }
    .chat .message .content .address {
        font-size: 15px;
        font-weight: 600;
        margin-right: 5px;
        color: white;
        text-decoration: none;
    }
    .chat .message .avatar {
        width: 35px;
        height: 35px;
        object-fit: contain;
    }

@media (max-width: 880px) {
    .Chat {
        display: none;
    }
}