.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 1;
    transition: opacity linear 150ms;
    background: rgba(0, 0, 0, 0.5);
    z-index: 1040;
    overflow-y: auto;
    display: none;
    justify-content: center;
}
    .modal .container {
        display: flex;
        margin: 3rem;
        min-height: calc(100vh - 6rem);
        align-items: center;
        justify-content: center;
    }
    .modal .overlay {
        background: rgba(0, 0, 0, 0.5);
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
    }
    .modal .wrapper {
        position: relative;
        display: flex;
        align-items: center;
        flex-direction: column;
        z-index: 1050;
        min-width: 300px;
        border-radius: 10px;
        background: #2c2d30;
        box-shadow: 0 8px 20px rgba(0,0,0,.6);
        font-weight: 600;
        padding: 40px 20px 20px;
        flex: 1 1;
        text-align: center;
    }
    .modal .closeB {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 10px;
        right: 10px;
        padding: 10px;
        cursor: pointer;
        background: none;
        border: none;
        border-radius: 6px;
    }
    .modal .closeB svg {
        fill: #777;
        transition: fill 200ms ease 0s;
    }
    .modal .status {
        text-align: center;
    }
    .modal .logo {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 90px;
        width: 90px;
        border-radius: 50%;
        background: #141515;
        margin-bottom: 10px;
        padding: 12px;
    }
    .modal .logo img {
        max-width: 100%;
    }
    .modal .title {
        font-weight: 600;
        font-size: 34px;
        line-height: 41px;
        margin-bottom: 27px;
        margin-top: 0;
        width: 100%;
        text-align: center;
        color: #fff;
    }
    .modal input {
        background-color: #404144;
        border: none;
        border-radius: 6px;
        color: #fff;
        cursor: pointer;
        display: flex;
        align-items: center;
        font-size: 16px;
        font-weight: 600;
        height: 50px;
        line-height: 50px;
        padding: 0 20px;
    }
    .modal .depositB {
        background-color: #59BD7D;
        margin-top: 20px;
        border: none;
        border-radius: 99px;
        color: #fff;
        cursor: pointer;
        display: flex;
        align-items: center;
        font-family: 'Inter', 'Roboto', 'Helvetica Neue', Helvetica, Arial, sans-serif;
        font-size: 16px;
        font-weight: 600;
        height: 50px;
        line-height: 50px;
        padding: 0 20px;
    }

@media (max-width: 880px) {
    .modal .container {
        width: 85vw;
        word-break: break-all;
    }
}
