#coinFlip {
    position: relative;
    height: 90px;
    width: 90px;
    transform-style: preserve-3d;
}
#coinFlip .coin0 img, #coinFlip .coin1 img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

#coinFlip .coin0, #coinFlip .coin1 {
    position: absolute;
    width: 90px;
    height: 90px;
}

#coinFlip .coin0 {
    transform: translateZ(1px);
    border-radius: 50%;
}

#coinFlip .coin1 {
    transform: translateZ(-1px) rotateY(180deg);
    border-radius: 50%;
}

#coinFlip.spin900 { 
    animation: rotate900 3s linear forwards;
}

#coinFlip.spin1080 { 
    animation: rotate1080 3s linear forwards;
}

#coinFlip.spin1260 { 
    animation: rotate1260 3s linear forwards;
}

#coinFlip.spin1440 { 
    animation: rotate1440 3s linear forwards;
}

#coinFlip.spin1620 { 
    animation: rotate1620 3s linear forwards;
}

#coinFlip.spin1800 { 
    animation: rotate1800 3s linear forwards;
}

#coinFlip.spin1980 { 
    animation: rotate1980 3s linear forwards;
}

#coinFlip.spin2160 { 
    animation: rotate2160 3s linear forwards;
}

@keyframes rotate900 {
    from { transform: rotateY(0); }
    to { transform: rotateY(900deg); }
}

@keyframes rotate1080 {
    from { transform: rotateY(0); }
    to { transform: rotateY(1080deg); }
}

@keyframes rotate1260 {
    from { transform: rotateY(0); }
    to { transform: rotateY(1260deg); }
}

@keyframes rotate1440 {
    from { transform: rotateY(0); }
    to { transform: rotateY(1440deg); }
}

@keyframes rotate1620 {
    from { transform: rotateY(0); }
    to { transform: rotateY(1620deg); }
}

@keyframes rotate1800 {
    from { transform: rotateY(0); }
    to { transform: rotateY(1800deg); }
}

@keyframes rotate1980 {
    from { transform: rotateY(0); }
    to { transform: rotateY(1980deg); }
}

@keyframes rotate2160 {
    from { transform: rotateY(0); }
    to { transform: rotateY(2160deg); }
}
