.Fairness {
    padding: 40px;
    width: calc(100% - 270px);
}

.Fairness a {
    border-bottom: 2px solid #59BD7D;
}
.term {
    border-bottom: 2px solid #59BD7D;
}

.hashInputs li::marker {
    color: #59BD7D;
}

.fairnessCode {
    background-color: #2c2d30;
    padding: 20px;
    color: #59bd7ebb;
}

.fairnessCode p {
    -webkit-user-select: text;
    -khtml-user-select: text;
    -moz-user-select: text;
    -o-user-select: text;
    user-select: text;
}

@media (max-width: 880px) {
    .Fairness {
        width: 100%;
        padding: 20px;
    }
}