.recentGames  {
    width: 100%;
    border-spacing: 0;
    border-radius: 5px;
}
.recentGames thead tr:first-child th:first-child {
    border-top-left-radius: 5px;
}
.recentGames thead tr:last-child th:last-child {
    border-top-right-radius: 5px;
}
.recentGames tr:last-child td:first-child {
    border-bottom-left-radius: 5px;
}
.recentGames tr:last-child td:last-child {
    border-bottom-right-radius: 5px;
}
.recentGames thead th {
    background-color: #141416;
    padding: 10px 0;
}
.recentGame {
    background-color: #141416;
    margin-bottom: 5px;
}
.recentGame .tdContent a {
    display: flex;
    justify-content: center;
    align-items: center;
}
.recentGame .user  {
    display: flex;
    align-items: center;
    justify-content: center;
}
.recentGame .user svg {
    width: 50px;
    height: 50px;
    object-fit: cover;
}
.recentGame .bet svg {
    width: 20px;
    height: 20px;
    margin-left: 5px;
    object-fit: cover;
}
.recentGame .coin img {
    width: 40px;
    height: 40px;
    object-fit: cover;
}

@media (max-width: 880px) {
    .recentGame .user .publicKey {
        display: none;
    }
    .recentGamesWrapper {
        display: flex;
        justify-content: center;
    }
}