.Navbar {
    background-color: #141416;
    position: fixed;
    width: 100vw;
    z-index: 1;
}
.Navbar .navbar {
    padding: 5px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
}
.wallet-adapter-button-trigger {
    background-color: #59BD7D;
}
    .Navbar .left {
        display: flex;
        align-items: center;
    }
    .Navbar .left .navigation {
        margin-left: 10px;
    }
    .Navbar .left .navigation .link:hover {
        border-bottom: 2px solid #746bf5;
    }
    .Navbar .navLinks {
        padding-left: 15px;
    }
    .Navbar .navLinks .inactiveNavLink {
        color: #f5f5f5c1
    }
    .Navbar .navLinks .inactiveNavLink:hover {
        color: #F5F5F5
    }
    .Navbar .navLinks .activeNavLink {
        color: #F5F5F5
    }
    .Navbar .logo {
        display: flex;
        align-items: center;
        border-right: 1.5px solid #ffffff47;
        padding-right: 15px;
    }
    .Navbar .logo .title {
        margin: 0;
        margin-left: 10px;
        font-size: 24px;
        font-family: 'Squared';
        font-weight: 200;
        letter-spacing: 3px;
    }
    .Navbar .logo img {
        width: 52px;
        height: 52px;
        object-fit: cover;
    }
    .Navbar .user {
        display: flex;
        align-items: center;

    }
    .Navbar .user .balance {
        height: 40px;
        width: 100px;
        display: flex;
        align-items: center;
        background-color: #1B1C21;
        border-radius: 99px;
        justify-content: center;
        padding: 5px 5px;
        margin-right: 15px;
    }
    .Navbar .user .balance svg {
        height: 20px;
        width: 20px;
        object-fit: cover;
    }
    .Navbar .user .balance .amount {
        padding: 5px;
    }
    .Navbar .user .balance .amount[data-balancechanged='1'] {
        animation: balanceIncrease 2.5s 1;
    }
    .Navbar .user .balance .amount[data-balancechanged='-1'] {
        animation: balanceDecrease 2.5s 1;
    }
    .Navbar .user .withdrawB,
    .Navbar .user .depositB {
        padding: 10px 30px;
        background-color: transparent;
        font-weight: 600;
        color: #F5F5F5;
        border: 2px solid #F5F5F5;
        margin-right: 15px;
        border-radius: 99px;
    }
    .Navbar .user .profile {
        display: flex;
        color: #F5F5F5;
        text-decoration: none;
        background-color: #1B1C21;
        border-radius: 5px;
        align-items: center;
        padding-right: 7px;
    }
    .Navbar .user .profile svg {
        width: 40px;
    }
    .Navbar .user .profile h4 {
        margin: 0;
    }

    .Navbar .pending {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .Navbar .pending .withdrawLabel {
        margin-right: 7px;
    }

@keyframes balanceIncrease {
    from {
        color: #F5F5F5;
    }
    50% {
        color: rgb(37, 185, 0);
    }
    to {
        color: #F5F5F5;
    }
}
@keyframes balanceDecrease {
    from {
        color: #F5F5F5;
    }
    50% {
        color: rgb(185, 37, 0);
    }
    to {
        color: #F5F5F5;
    }
}

@media (max-width: 880px) {
    .Navbar .user {
        display: none;
    }
    .Navbar .wallet-adapter-button-trigger {
        display: none;
    }
}